// MAIN //

/**
* Returns the [viewport width][1].
*
* [1]: https://stackoverflow.com/questions/1248081/how-to-get-the-browser-viewport-dimensions/8876069#8876069
*
* @private
* @returns {(number|null)} viewport width
*/
function viewportWidth() {
	// "Sniff" whether we are in a browser...
	if ( typeof window === 'undefined' ) {
		return null;
	}
	return Math.max( document.documentElement.clientWidth, window.innerWidth || 0 );
}


// EXPORTS //

export default viewportWidth;
